import React from "react";
import "./styles.css";
import productImg from "./assets/product-img.webp";

const ImageProduct = ({disable}) => {
  return (
    <div>
      {" "}
      {disable ? (
        <img src={productImg} className="card-img-top-disable" />
      ) : (
        <img src={productImg} className="card-img-top" />
      )}
    </div>
  );
};

export default ImageProduct;
